.moderatori {
	width: 100%;
}

.moderatori h1 {
	text-align: center;
	margin-top: 5%;
}

.moderatori table tr {
	width: 100%;
	height: 40px;
}

.moderatori table tr:hover {
	background-color: #eee;
}

.moderatori table tr td {
	width: 33%;
}

.moderatori table {
	width: 80%;
	margin-left: 10%;
	border-collapse: collapse;
	border-top: 3px solid darkblue;
}

.moderatori table tr td {
	text-align: center;
}

.linkDodaj {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-top: 30px;
}

.moderatori table tr:first-child {
	background-color: lightgrey;
	border-style: 1px solid lightgrey;
}

.linkDodaj .link {
	height: 40px;
	outline: none;
	font-weight: 700;
	font-size: 14px;
	border-radius: 3px;
	width: 30%;
	background-color: darkblue;
	color: white;
	text-decoration: none;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.linkDodaj .link i {
	margin-right: 5%;
	color: white;
}

.moderatori .fas {
	color: black;
}

.moderatori button {
	background-color: white;
	border: none;
	outline: none;
	width: 50%;
}

.moderatori button i {
	margin-right: 10px;
}

.moderatori table tr:hover button {
	background-color: #eee;
}

.moderatori button:hover {
	font-weight: 700;
	font-size: 25px;
}

.moderatori .editModeratora {
	text-decoration: none;
	color: black;
}

.moderatori .editModeratora:hover {
	font-weight: 700;
	font-size: 25px;
}

.moderatori .editModeratora i {
	margin-right: 10px;
}

.moderatori .search {
	margin-left: 10%;
	margin-top: 2%;
	border: none;
	border-bottom: 1px solid grey;
	margin-bottom: 5px;
	width: 80%;
	outline: none;
	padding: 7px 10px;
}


