.uspomene {
	width: 100%;
}

.uspomene h1 {
	text-align: center;
	margin-top: 5%;
}

.uspomene table tr {
	width: 100%;
	height: 40px;
}

.uspomene table tr:first-child {
	background-color: lightgrey;
}

.uspomene table tr:hover {
	background-color: #eee;
}

.uspomene table tr:hover button {
	background-color: #eee;
}

.uspomene table tr td {
	width: 25%;
}

.uspomene table {
	border-collapse: collapse;
	width: 80%;
	margin-left: 10%;
	border-top: 3px solid darkblue; 
}

.uspomene table tr td {
	text-align: center;
}

.uspomene .edit {
	color: black;
	text-decoration: none;
}

.uspomene .edit i {
	margin-right: 10px;
}

.uspomene .edit:hover {
	font-weight: 700;
	font-size: 25px;
}

.uspomene button {
	border: none;
	outline: none;
	width: 50%;
	background-color: white;
}

.uspomene button i {
	margin-right: 10px;
}

.uspomene button:hover {
	font-weight: 700;
	font-size: 25px;
}

.narudzbe .search {
	margin-left: 10%;
	margin-top: 2%;
	border: none;
	border-bottom: 1px solid grey;
	margin-bottom: 5px;
	width: 80%;
	outline: none;
	padding: 7px 10px;
}


