.pregledUspomena input {
	outline: none;
	border: none;
	border-bottom: 0.5px solid black;
	height: 30px;
	padding-left: 7px;
	margin-bottom: 25px;


}

.prUsp {
	width: 50%; 
	padding-left: 5%;
}

.prUsp h2, .prUsp h4 {
	font-size: 15px;
}

.pretraga {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	max-height: 550px;
	margin-left:1%;
	width: 18%;
	padding: 10px;
	position: sticky;
	position: -webkit-sticky;
	top: 0;
}

.umrli {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	margin-left: 5%;
}


.pregledUspomene {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	background-color: #f4f4f4;
	border:2px solid #65594d;
	margin-bottom:10px;
}

.pregledUspomena h1 {
	margin-top: 2%;
}


.pretraga, .pretraga input{
	background-color:#65594d;
	color:#f4f4f4;
}


.pregled{
	width: 70%;
	margin: 0px auto 0px auto;
}

@media only screen and (max-width: 900px){
	.umrli {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.pretraga {
		position: relative;
		width: 90%;
		margin:auto;
	}

	.pregledUspomene {
		-ms-flex-direction: column-reverse;
		    flex-direction: column-reverse;
		width: 100%;
	}

}



