.index2 {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
}

.navbar2 {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 20%;
	height: 100vh;
	background-color: #eee;
	position: fixed;
}

.navbar2 *:not(:last-child) {
	margin-top: 10px;
	margin-left: 5%;
	width: 90%;
	height: 40px;
	text-decoration: none;
	color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	background-color: #ddd;
	border-radius: 3px;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-transition-duration: 0.4s;
	     -o-transition-duration: 0.4s;
	        transition-duration: 0.4s;
}

.navbar2 button {
	-ms-flex-item-align: center;
	    align-self: center;
	border: none;
	width: 90%;
	height: 40px;
	margin-top: 5%;
	outline: none;
	-webkit-transition-duration: 0.4s;
	     -o-transition-duration: 0.4s;
	        transition-duration: 0.4s;
	background-color: #ddd;
	border-radius: 3px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	display: none;
}

.navbar2 *:last-child {
	margin-bottom: 5%;
}

.navbar2 *:hover {
	background-color: lightblue;
}

.navbar2 * i {
	margin-top: 5%;
	margin-left: 10px;
	margin-right: 10px;
}

.page {
	width: 80%;
	margin-left: 20%;
}

@media only screen and (max-width: 768px) {
	.index2 {
		-ms-flex-direction: column;
		    flex-direction: column;
	}

	.navbar2 {
		position: relative;
		height: 100%;
		width: 100%;
	}

	.page {
		margin: auto;
		margin-bottom: 5%;
	}
}


