.narucistr{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	-ms-flex-direction:column;
	    flex-direction:column;
}

.forma-front {
	text-align:left;
	font-size:18px;
	padding-left:25%;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	padding-bottom:20%;
	color:black;
}

.napomena, .bio {
	height:130px; width:600px;
	padding:0;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
	padding: 12px 20px;
	resize: none;
}

 


#formahead{
	display:block;
	text-align:center;
	padding-top:15px;
	padding-bottom: 5%;
	background: -webkit-gradient(linear, left top, left bottom, from(#000), to(#fff));
	background: -webkit-linear-gradient(#000, #fff);
	background: -o-linear-gradient(#000, #fff);
	background: linear-gradient(#000, #fff);
}

.formanaslov{
	color:#fff;
}

.narudzbausluga{
	font-weight: 1000;
}

@media screen and (max-width: 600px) {
	.narucistr{
		display: -ms-flexbox;
		display: flex;
		width:100%;
		text-align:center;
		padding:0;
		margin:0;
	}

	.napomena, .bio{
		width:100%;
	}

	.forma-front{
		padding:0;
	}
}


