.sifra {
  width: 100%;
  margin-top: 5%;
}

.sifra h1 {
  margin-top: 100%;
}

.sifra form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: auto;
  width: 40%;
}

.sifra form label {
  margin-top: 10px;
}

.sifra input {
  border-radius: 2px;
  border: 1px solid lightgrey;
  height: 30px;
  padding-left: 10px;
}

.sifra button {
  border: none;
  height: 40px;
  padding-left: 10px;
  margin-top: 30px;
  background-color: darkblue;
  color: white;
  outline: none;
  width: 100%;
  cursor: pointer;
  border-radius: 3px;
  margin-bottom: 30px;
  font-weight: 700;
}

.sifra button i {
  margin-right: 5%;
}

.sifra h1 {
  text-align: center;
  margin-top: 5%;
}

.sifra textarea {
  border-radius: 2px;
  border: 1px solid lightgrey;
  padding-left: 10px;
  padding-top: 10px;
  font-family: sans-serif;
}


