.narudzbe {
	width: 100%;
}

.narudzbe h1 {
	text-align: center;
	margin-top: 5%;
}

.narudzbe table tr {
	width: 100%;
	height: 40px;
}

.narudzbe table tr:first-child {
	background-color: lightgrey;
}

.narudzbe table tr:hover {
	background-color: #eee;
}

.narudzbe table tr:hover button {
	background-color: #eee;
}

.narudzbe table tr td {
	width: 20%;
}

.narudzbe table {
	border-collapse: collapse;
	width: 80%;
	margin-left: 10%;
	border-top: 3px solid darkblue; 
}

.narudzbe table tr td {
	text-align: center;
}

.narudzbe .detaljno {
	color: black;
}

.narudzbe button {
	border: none;
	outline: none;
	width: 50%;
	background-color: white;
}

.narudzbe button i, .narudzbe .detaljno i {
	margin-right: 10px;
}

.narudzbe button:hover, .narudzbe .detaljno {
	font-weight: 700;
	font-size: 25px;
}

.uspomene .search {
	margin-left: 10%;
	margin-top: 5%;
	border: none;
	border-bottom: 1px solid grey;
	margin-bottom: 5px;
	width: 80%;
	outline: none;
	padding: 7px 10px;
}

.uspomene .search {
	margin-left: 10%;
	margin-top: 5%;
	border: none;
	border-bottom: 1px solid grey;
	margin-bottom: 5px;
	width: 80%;
	outline: none;
	padding: 7px 10px;
}


