@import url('https://fonts.googleapis.com/css?family=Lato&subset=latin-ext');

.cmslogin {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100vw;
	height: 100vh;
	background-color: #eee;
	font-family: 'Lato', sans-serif;
}

.cmslogin form {
	background-color: white;
	border: 1px solid black;
	border-radius: 5px;
	border-top: 5px solid purple;
	min-width: 33%;
	height: 70%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	margin-top: 5%;
}

.cmslogin form input {
	height: 7%;
	margin-bottom: 3%;
	margin-top: 3%;
	width: 70%;
	border-radius: 2px;
	border: 1px solid grey;
}

.cmslogin form input:not([type="submit"]) {
	padding-left: 5%;
}

.cmslogin form input[type="submit"] {
	height: 7%;
	background-color: purple;
	border: none;
	color: white;
	width: 75%;
}

.cmslogin form label:first-child {
	margin-top: 20%;
}

@media only screen and (max-width: 768px) {
	.cmslogin form {
		width: 60%;
	}
}

@media only screen and (max-width: 500px) {
    .login form {
    	width: 80%;
    }
}

.login input[type="submit"] {
	font-size: 13px;
	font-weight: 700;
}


